import Vue from 'vue'
import Router from 'vue-router'
import Error404 from '@/views/Error404.vue'
import AppLoading from '@/components/AppLoading.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      component: Error404
    },
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: () => ({
        component: import('@/views/AppHome.vue'),
        loading: AppLoading
      })
    },
    {
      path: '/music',
      component: () => ({
        component: import('@/views/AppMusic.vue'),
        loading: AppLoading
      })
    }
  ]
})
