<template>
  <div class="app-loading">
    <div class="round-container">
      <div id="round-a" class="round"></div>
      <div id="round-b" class="round"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app-loading',

  mounted () {
    let parentNode = this.$parent.$el
    let parentNodePosition = window.getComputedStyle(parentNode).getPropertyValue('position')
    if (parentNodePosition === 'static' || parentNodePosition === '') {
      parentNode.style.position = 'relative'
    }
  }
}
</script>

<style scoped>
.app-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, .8);
  z-index: 9999;
}

.round-container {
  position: relative;
  width: 160px;
  height: 160px;
}

.round {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #2196f3;
  opacity: .6;
  border-radius: 50%;
}

#round-a {
  animation: bounce 2s ease-in-out infinite;
}

#round-b {
  animation: bounce 2s -1s ease-in-out infinite;
}

@keyframes bounce {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
</style>
