<template>
  <div id="app-error-404">
    <img
      :class="enableAnimation ? classImage : ''"
      src="../assets/page_not_found.png"
      alt="404"
      @animationend="animationOff"
    >
  </div>
</template>

<script>
export default {
  name: 'app-error-404',

  data () {
    return {
      enableAnimation: true,
      classImage: [
        'animated',
        'tada'
      ]
    }
  },

  beforeRouteUpdate (to, from, next) {
    this.animationOn()
    next()
  },

  methods: {
    animationOn () {
      this.enableAnimation = true
    },
    animationOff () {
      this.enableAnimation = false
    }
  }
}
</script>

<style scoped>
#app-error-404 {
  overflow: hidden;
}

#app-error-404 > img {
  display: block;
  margin: 32px auto;
  width: 100%;
  max-width: 640px;
  transition: transform 0.8s;
}
</style>
